<template>
  <div class="myHeaderBox">
    <div class="mycell myMenu">
      <i class="el-icon-menu"></i>
    </div>
    <div class="mycell myUser">
      <i class="el-icon-search"></i>
      <i class="el-icon-share"></i>
      <i class="el-icon-setting"></i>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  methods: {
    liClickFn() {
      const elementArr = this.$refs;
      for (let elementObj in elementArr) {
        elementArr[elementObj][0].classList.remove("isActive");
      }
      event.srcElement.classList.add("isActive");
    }
  }
};
</script>
<style socped>
.myHeaderBox {
  width: 100%;
  height: 60px;
  line-height: 60px;
  padding: 0 10px;
  font-size: 36px;
  color: #fff;
}
.myHeaderBox .mycell {
  display: inline-block;
  float: left;
  width: calc(50% - 10px);
  border: 0;
}
.myHeaderBox .myMenu {
  text-align: left;
}
.myHeaderBox .myUser {
  text-align: right;
}
</style>



